import React from 'react';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AppCard } from '../components';
import { GameService, IGame } from '../service';

import SportsEsportsIcon from '@material-ui/icons/SportsEsports';

import Container from '@material-ui/core/Container';
import { Box, Typography } from '@material-ui/core';

const Header = () => {
  return (
    <Box
      style={{
        padding: '1rem',
        display: 'flex'
      }}
    >
      <React.Fragment>
        <SportsEsportsIcon
          style={{
            fontSize: '2rem',
            marginRight: '1rem'
          }}
        />
        <Typography variant='h4' component='h4'>
          Rating Games ;)
        </Typography>
      </React.Fragment>
    </Box>
  );
};

export default class MainPage extends React.Component<
  {},
  { games: IGame[]; onDestroy: Subject<void> }
> {
  state = {
    games: [],
    onDestroy: new Subject<void>()
  };

  public componentDidMount(): void {
    GameService.gameList$
      .pipe(takeUntil(this.state.onDestroy))
      .subscribe((games: IGame[]): void => {
        this.setState(state => {
          return {
            ...state,
            games
          };
        });
      });
  }

  public componentWillMount(): void {
    this.state.onDestroy.next();
  }

  render(): React.ReactNode {
    return (
      <Container maxWidth='xl'>
        <Header />
        <Box
          style={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'center'
          }}
        >
          {this.state.games.map((game: IGame, index: number) => (
            <AppCard {...game} key={index} />
          ))}
        </Box>
        <Box
          style={{
            height: '3rem',
            backgroundColor: 'tomato'
          }}
        >
          Footer
        </Box>
      </Container>
    );
  }
}
